"use client";

import { useEffect, useState } from "react";
import { usePage } from "@/hooks/usePage";
import { Card, CardContent } from "@/components/ui/Card";
import { Button } from "@/components/ui/Button";
// import { addNotification } from "@/hooks/useNotification";
// import coursesData from "@/data/courses.json";
import { useNotifications } from "@/hooks/useNotification";

interface Course {
  course_code: string;
  course_title: string;
  credit_unit: number;
  semester: string;
  level: number;
  department: string;
  status: string;
}

export default function CourseRegistrationPage() {
  const { setPage } = usePage();
  const [availableCourses, setAvailableCourses] = useState<Course[]>([]);
  const [registeredCourses, setRegisteredCourses] = useState<Course[]>([]);
  const [totalUnits, setTotalUnits] = useState<number>(0);
  const [finalized, setFinalized] = useState<boolean>(false);
  const { addNotification } = useNotifications()

  useEffect(() => {
    setPage("Course Registration");
    // Normally fetch from backend using student's department, level & semester
    setAvailableCourses([
  {
    "course_code": "CSC201",
    "course_title": "Data Structures",
    "credit_unit": 3,
    "semester": "First",
    "level": 200,
    "department": "Computer Science",
    "status": "Core"
  },
  {
    "course_code": "CSC204",
    "course_title": "Database Systems",
    "credit_unit": 3,
    "semester": "First",
    "level": 200,
    "department": "Computer Science",
    "status": "Elective"
  }
]
);
  }, [setPage]);

  useEffect(() => {
    const total = registeredCourses.reduce((acc, c) => acc + c.credit_unit, 0);
    setTotalUnits(total);
  }, [registeredCourses]);

  const handleRegister = (course: Course) => {
    if (finalized) return;
    if (registeredCourses.find((c) => c.course_code === course.course_code)) {
      addNotification({variant: "warning", message: "Course already registered"});
      return;
    }
    if (totalUnits + course.credit_unit > 24) {
      addNotification({variant: "error", message: "Maximum credit unit exceeded (24 units)"});
      return;
    }
    setRegisteredCourses([...registeredCourses, course]);
    addNotification({variant: "success", message:`${course.course_code} added`});
  };

  const handleDrop = (course: Course) => {
    if (finalized) return;
    setRegisteredCourses(registeredCourses.filter((c) => c.course_code !== course.course_code));
    addNotification({ message: `${course.course_code} dropped`, variant: 'info'});
  };

  const handleSubmit = () => {
    if (totalUnits < 15) {
      addNotification({variant: "error", message: "Minimum 15 units required before submission"});
      return;
    }
    setFinalized(true);
    addNotification({variant: "success", message: "Registration submitted successfully!"});
  };

  return (
    <div className="max-w-6xl mx-auto p-6 min-h-screen">
      <div className="grid md:grid-cols-2 gap-6">
        {/* AVAILABLE COURSES */}
        <Card className="shadow-md rounded-xl">
          <CardContent className="p-4">
            <h2 className="text-xl font-semibold mb-3 text-primary">Available Courses</h2>
            <div className="overflow-x-auto">
              <table className="w-full border border-gray-200 text-sm">
                <thead className="bg-secondary text-textOnPrimary">
                  <tr>
                    <th className="p-2 text-left">Code</th>
                    <th className="p-2 text-left">Title</th>
                    <th className="p-2 text-center">Unit</th>
                    <th className="p-2 text-center">Action</th>
                  </tr>
                </thead>
                <tbody>
                  {availableCourses.map((course) => (
                    <tr key={course.course_code} className="border-b hover:bg-surfaceElevated transition">
                      <td className="p-2">{course.course_code}</td>
                      <td className="p-2">{course.course_title}</td>
                      <td className="p-2 text-center">{course.credit_unit}</td>
                      <td className="p-2 text-center">
                        <Button
                          disabled={finalized}
                          onClick={() => handleRegister(course)}
                          className="bg-primary text-textOnPrimary"
                        >
                          Register
                        </Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </CardContent>
        </Card>

        {/* REGISTERED COURSES */}
        <Card className="shadow-lg rounded-xl">
          <CardContent className="p-4">
            <h2 className="text-xl font-semibold mb-3 text-primary">Registered Courses</h2>
            {registeredCourses.length === 0 ? (
              <p className="text-gray-500 text-sm">No courses registered yet.</p>
            ) : (
              <div className="overflow-x-auto">
                <table className="w-full border border-gray-200 text-sm">
                  <thead className="bg-secondary text-textOnPrimary">
                    <tr>
                      <th className="p-2 text-left">Code</th>
                      <th className="p-2 text-left">Title</th>
                      <th className="p-2 text-center">Unit</th>
                      <th className="p-2 text-center">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {registeredCourses.map((course) => (
                      <tr key={course.course_code} className="border-b hover:bg-surfaceElevated">
                        <td className="p-2">{course.course_code}</td>
                        <td className="p-2">{course.course_title}</td>
                        <td className="p-2 text-center">{course.credit_unit}</td>
                        <td className="p-2 text-center">
                          {!finalized && (
                            <Button
                              onClick={() => handleDrop(course)}
                              className="bg-destructive text-textOnPrimary"
                            >
                              Drop
                            </Button>
                          )}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
            <div className="flex justify-between items-center mt-4">
              <p className="font-semibold text-accent">Total Units: {totalUnits}</p>
              {!finalized ? (
                <Button onClick={handleSubmit} className="bg-accent text-textOnPrimary">
                  Submit Registration
                </Button>
              ) : (
                <span className="text-green-600 font-medium">âœ” Finalized</span>
              )}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
